function useTodoLists(currentUserId) {
  const [todoLists, setTodoLists] = React.useState([]);

  // useEffect(() => {
  //   firebase
  //     .firestore()
  //     .collection(currentUserId)
  //     .onSnapshot((snapShot) => {
  //       const newTodoLists = [];
  //       snapShot.docs.map((doc) => {
  //         let tempData = {};
  //         tempData = doc.data();
  //         tempData.id = doc.id;
  //         newTodoLists.push(tempData);
  //       })
  //       setTodoLists(newTodoLists);
  //     })
  // }, []);
  useEffect(() => {
    firebase
      .firestore()
      .collection('boards').where('assignments','array-contains', currentUserId)
      .get()
      .then((snapShot) => {
        const newTodoLists = [];
        snapShot.docs.map((doc) => {
          let tempData = {};
          tempData = doc.data();
          tempData.id = doc.id;
          newTodoLists.push(tempData);
        })
        setTodoLists(newTodoLists);
      }).catch((error) => {
        alert('error getting documents');
        console.log("Error getting documents: ", error);
    });
  }, []);
  return todoLists;
}

