import React, {useState} from 'react'
import {Avatar, Button, FormControl, Input, Snackbar, InputLabel, Paper, Typography} from '@material-ui/core'
import { Alert } from "@material-ui/lab";
import LockOutlinedIcon from '@material-ui/icons/LockOutlined'
import withStyles from '@material-ui/core/styles/withStyles'
import {Link, withRouter} from 'react-router-dom'
import firebase from '../../services/firebase'

const styles = theme => ({
  main: {
    width: 'auto',
    display: 'block',
    marginLeft: theme.spacing.unit * 3,
    marginRight: theme.spacing.unit * 3,
    [theme.breakpoints.up(400 + theme.spacing.unit * 3 * 2)]: {
      width: 400,
      marginLeft: 'auto',
      marginRight: 'auto',
    },
  },  
  paper: {
    marginTop: theme.spacing.unit * 8,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    padding: `${theme.spacing.unit * 2}px ${theme.spacing.unit * 3}px ${theme.spacing.unit * 3}px`,
  },
  avatar: {
    margin: theme.spacing.unit,
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing.unit,
  },
  submit: {
    marginTop: theme.spacing.unit * 3,
  },
  createAccount: {
    background: '#41b5f0',
    marginTop: '10px',
  },
});

function SignIn(props) {
  const {classes} = props
  
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')

  const [alertData, setAlertData] = useState('success');
  const [alertType, setAlertType] = useState('success');
  const [alertOpen, setAlertOpen] = useState(false);  
  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setAlertOpen(false);
  };
  
  return (
    <main className={classes.main}>
      <Paper className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon/>
        </Avatar>
        <Typography component="h1" variant="h5">
          Sign in
        </Typography>
        <form className={classes.form} onSubmit={e => e.preventDefault() && false}>
          <FormControl margin="normal" required fullWidth>
            <InputLabel htmlFor="email">Email Address</InputLabel>
            <Input id="email" name="email" autoComplete="on" autoFocus value={email}
                   onChange={e => setEmail(e.target.value)}/>
          </FormControl>
          <FormControl margin="normal" required fullWidth>
            <InputLabel htmlFor="password">Password</InputLabel>
            <Input name="password" type="password" id="password" autoComplete="off" value={password}
                   onChange={e => setPassword(e.target.value)}/>
          </FormControl>
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            onClick={login}
            style={{textDecoration:'none', color: 'white'}}
            className={classes.submit}>
            Sign in
          </Button>
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="blue"
            component={Link}
            to="/register"
            style={{textDecoration:'none', color: 'white'}}
            className={classes.createAccount}>
            Create Account
          </Button>
        </form>
        <Button
            type="submit"
            fullWidth
            onClick={handleForgotPassword}
            variant="outlined"
            color="primary"
            style={{textDecoration:'none', color: '#3dd19d'}}
            className={classes.submit}>
            Forgot Password?
          </Button>
      </Paper>
      <Snackbar
                open={alertOpen}
                autoHideDuration={6000}
                onClose={handleClose}
                // anchorOrigin={{vertical: 'top', horizontal:'center'}}
              >
          <Alert
                  variant="filled"
                  onClose={handleClose}
                  severity={alertType}
                >
                  {alertData}
                </Alert>
      </Snackbar>
    </main>
  )

  async function handleForgotPassword(){
    if(email.length>10 && email.includes('@') && email.includes('.')){
      await firebase.auth().sendPasswordResetEmail(email).then(async ()=> {
        await setAlertData('Email sent. Kindly check you email inbox after a few moments.')
        await setAlertType('success')
        await setAlertOpen('true');
      }).catch(async (err)=>{
        await setAlertData(err.message)
        await setAlertType('error')
        await setAlertOpen('true');
      });
    }
    else{
      await setAlertData('Please enter a valid email address')
      await setAlertType('error')
      await setAlertOpen('true');
    }
}
  
  async function login() {
    try {
      // await firebase.login(email, password)
      await firebase.auth().signInWithEmailAndPassword(email, password);
      const isVerified = await firebase.auth().currentUser.emailVerified;
      if(!isVerified){
        await firebase.auth().currentUser.sendEmailVerification();
        
        await setAlertType('error');
        await setAlertData('Please check your mailbox for the confirmation email.');
        await setAlertOpen(true);
        
        await firebase.auth().signOut();
        return;
      }
  // eslint-disable-next-line react-hooks/rules-of-hooks  
    props.history.replace('/home');
    props.history.go(0);
    } catch (error) {
      await setAlertData(error.message)
      await setAlertType('error')
      await setAlertOpen('true');
    }
  }
}

export default withRouter(withStyles(styles)(SignIn))
