function useTodoLists(currentUserId, setLoaded) {
  const [todoLists, setTodoLists] = React.useState([]);

  // useEffect(() => {
  //   firebase
  //     .firestore()
  //     .collection(currentUserId)
  //     .onSnapshot((snapShot) => {
  //       const newTodoLists = [];
  //       snapShot.docs.map((doc) => {
  //         let tempData = {};
  //         tempData = doc.data();
  //         tempData.id = doc.id;
  //         newTodoLists.push(tempData);
  //       })
  //       setTodoLists(newTodoLists);
  //     })
  // }, []);
  useEffect(() => {
    firebase
      .firestore()
      .collection("boards")
      .where("assignments", "array-contains", currentUserId)
      .get()
      .then((snapShot) => {
        const newTodoLists = [];
        snapShot.docs.map((doc) => {
          let tempData = {};
          tempData = doc.data();
          tempData.id = doc.id;
          newTodoLists.push(tempData);
        });
        setTodoLists(newTodoLists);
        setLoaded(true);
      })
      .catch((error) => {
        alert("error getting documents");
        console.log("Error getting documents: ", error);
      });
  }, []);
  return todoLists;
}

const Profile = (props) => {
  const currentUserId = props.currentUserId; //firebase.auth().currentUser.email;
  const userUid = firebase.auth().currentUser.uid;

  const currentUserEmail =
    firebase.auth().currentUser && firebase.auth().currentUser.email;
  const currentUserName =
    firebase.auth().currentUser && firebase.auth().currentUser.displayName;
  if (!currentUserId) {
    // not logged in
    alert("Please login first");
    props.history.replace("/");

    return null;
  }

  // eslint-disable-next-line react-hooks/rules-of-hooks
  const [loaded, setLoaded] = useState(false);
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const [position, setPosition] = useState("");
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const [name, setName] = useState("");
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const [imgUrl, setImgUrl] = useState("");

  // eslint-disable-next-line react-hooks/rules-of-hooks
  const tasksData = useTodoLists(currentUserId, setLoaded);
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const [numOfAssigned, setNumOfAssigned] = useState(0);
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const [numOfAssignee, setNumOfAssignee] = useState(0);
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const [totalTasks, setTotalTasks] = useState(0);
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const [remainingAssigneeTasks, setRemainingAssigneeTasks] = useState(0);
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const [completedTasks, setCompletedTasks] = useState(0);

  // console.log('dataaa', tasksData);

  // eslint-disable-next-line react-hooks/rules-of-hooks
  useEffect(() => {
    firebase
      .firestore()
      .collection("users")
      .doc(userUid)
      .get()
      .then(async (doc) => {
        await setPosition(doc.data().position);
        await setName(doc.data().name);
        await setImgUrl(doc.data().imgUrl);
      })
      .catch((err) => console.log(err));
  }, []);

  //   // eslint-disable-next-line react-hooks/rules-of-hooks
  //   useEffect(()=>{
  //     setImgUrl(imgUrl);
  //   },[imgUrl])

  // eslint-disable-next-line react-hooks/rules-of-hooks
  useEffect(() => {
    if (tasksData.length > 0) {
      console.log("dataaa", tasksData);
      tasksData.map(async (boardObj) => {
        // let toto = totalTasks+boardObj.todoList.length;
        await setTotalTasks(
          (prevState) => prevState + boardObj.todoList.length
        );
        // console.log(await totalTasks);
        boardObj.todoList.map(async (todoListObj) => {
          if (
            todoListObj.assignedBy === currentUserId &&
            todoListObj.assignedTo !== "" &&
            todoListObj.assignedTo !== currentUserId
          )
            await setNumOfAssigned((prevState) => prevState + 1);
          if (
            todoListObj.assignedTo === currentUserId 
            // todoListObj.assignedBy !== currentUserId && // CHANGE THIS SO BOTH ASSIGNEE SELF AND ASSIGNEE OF OTHERS ARE DIFFERENT METRICS
            // todoListObj.assignedBy !== ""
          ) {
            await setNumOfAssignee((prevState) => prevState + 1);
            if (todoListObj.isComplete) {
              await setCompletedTasks((prevState) => prevState + 1);
            } else {
              await setRemainingAssigneeTasks((prevState) => prevState + 1);
            }
          }
        });
      });
      // console.log('assigned:', numOfAssigned, 'assignee:', numOfAssignee, 'total:',
      // totalTasks, 'remaining:', remainingAssigneeTasks, 'completedTasks', completedTasks);
    }
  }, [tasksData.length]);

  return loaded ? (
    <div className="ProfileComponent">
      <Container
        maxWidth="lg"
        // style={{background:'yellow'}}
      >
        <Grid container spacing={3}>
          <Grid item xs={4}>
            <Paper></Paper>
          </Grid>
          <Grid item xs={4} lg={5}>
            <Paper>
              <Box
                p={10}
                m={2}
                pt={2}
                width="100%"
                display="flex"
                flexDirection="row"
                justifyContent="center"
                alignItems="center"
                paddingBottom="20px"
              >
                <Avatar
                  style={{ width: "80px", height: "80px", marginRight: "15px" }}
                  alt=""
                  src={imgUrl}
                />
                {console.log("URLLLLLLL", imgUrl)}
                <ul>
                  <li>
                    <Typography>{name}</Typography>
                  </li>
                  <li>
                    <Typography>{position}</Typography>
                  </li>
                </ul>
              </Box>
              <Box marginLeft={10}>
                <Typography>
                  Still working on this page... X-D - Danny
                </Typography>
              </Box>

              <Box marginLeft={10}>
                <ul>
                  <li>
                    <Typography>My tasks: {totalTasks}</Typography>
                  </li>
                  <li>
                    <Typography>Assigned to others: {numOfAssigned}</Typography>
                  </li>
                  <li>
                    <Typography>
                      Tasks assigned to me: {numOfAssignee}
                    </Typography>
                  </li>
                  <li>
                    <Typography>
                      Assigned to me completed: {completedTasks}
                    </Typography>
                  </li>
                  <li>
                    <Typography>
                      Assigned to me remaining: {remainingAssigneeTasks}
                    </Typography>
                  </li>
                </ul>
              </Box>
            </Paper>
          </Grid>
          <Grid item xs={4}>
            <Paper></Paper>
          </Grid>
        </Grid>
      </Container>
    </div>
  ) : (
    <div id="loader">
      <CircularProgress style={{ color: "#3dd19d" }} />
    </div>
  );
};

export default Profile;
