import React, { useState, useEffect } from "react";
import {
  Box,
  Button,
  Snackbar,
} from "@material-ui/core";

import { Alert } from "@material-ui/lab";

import { withStyles, makeStyles } from '@material-ui/core/styles';

import DeleteIcon from "@material-ui/icons/Delete";

import Modal from "@material-ui/core/Modal";
import Backdrop from "@material-ui/core/Backdrop";
import Fade from "@material-ui/core/Fade";

const useStyless = makeStyles((theme) => ({
    modal: {
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
    },
    paper: {
      display: 'flex',
      flexDirection: 'column',
      justifyContent: 'center',
      alignItems: 'center',
      backgroundColor: theme.palette.background.paper,
      border: "2px solid #000",
      boxShadow: theme.shadows[5],
      padding: theme.spacing(5, 5, 5),
    },
  }));

const BoardDeleteConfirm = ({ openDeleteModal, setOpenDeleteModal, list, index, removeTodoList })=> {
    const classes = useStyless();
    // const [alertOpen, setAlertOpen] = useState(false);
    // const [severity, setSeverity] = useState("success");
    // const [alertMessage, setAlertMessage] = useState("");
  
    // const handleOpen = () => {
    //   setOpen(true);
    // };
  
    const handleClose = () => {
      setOpenDeleteModal(false);
    };
  
    // const handleCloseAlert = (event, reason) => {
    //   if (reason === "clickaway") {
    //     return;
    //   }
    //   setAlertOpen(false);
    // };
  
  
    
    return (
      <div>
    
        <Modal
          aria-labelledby="transition-modal-title"
          aria-describedby="transition-modal-description"
          className={classes.modal}
          open={openDeleteModal}
          onClose={handleClose}
          closeAfterTransition
          BackdropComponent={Backdrop}
          BackdropProps={{
            timeout: 500,
          }}
        >
          <Fade in={openDeleteModal}>
            <div className={classes.paper}>
              <h4 id="transition-modal-title">
                This will delete the board permanently, are you sure?
              </h4>
              <Box mt={2}>
                <Button
                  onClick={(event) => {
                        removeTodoList(event, list, index);
                        handleClose();
                      }}
                  style={{color:'white', background:'grey'}}
                  variant="contained"
                >
                <DeleteIcon style={{marginRight:'10px'}} />
                 Delete Board
                </Button>
              </Box>
            </div>
          </Fade>
        </Modal>
        {/* <Snackbar
          open={alertOpen}
          autoHideDuration={4000}
          onClose={handleCloseAlert}
        >
          <Alert variant="filled" onClose={handleCloseAlert} severity={severity}>
            {alertMessage}
          </Alert>
        </Snackbar> */}
      </div>
    );
  }

  export default BoardDeleteConfirm;